// frontend/src/Graph.js
import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import { Chart as ChartJS } from "chart.js/auto";
import io from "socket.io-client"; // Correct import for frontend

const socket = io("http://localhost:5000"); // Connect to backend WebSocket server

const Graph = () => {
  const [labels, setLabels] = useState([]);
  const [voltageData, setVoltageData] = useState([]);
  const [currentData, setCurrentData] = useState([]);

  useEffect(() => {
    socket.on("serialData", ({ voltage, current, time }) => {
      setLabels((prev) => [...prev.slice(-19), time]);
      setVoltageData((prev) => [...prev.slice(-19), voltage]);
      setCurrentData((prev) => [...prev.slice(-19), current]);
    });

    return () => {
      socket.off("serialData"); // Cleanup when component unmounts
    };
  }, []);

  const data = {
    labels,
    datasets: [
      {
        label: "Voltage (V)",
        data: voltageData,
        borderColor: "blue",
        fill: false,
      },
      {
        label: "Current (A)",
        data: currentData,
        borderColor: "green",
        fill: false,
      },
    ],
  };

  return <Line data={data} />;
};

export default Graph;
