import React, { useState, useEffect } from "react";
import Graph from "./Graph";
import io from "socket.io-client";

const socket = io("http://localhost:5000");

const App = () => {
  const [activeTab, setActiveTab] = useState("current");
  const [interval, setIntervalValue] = useState(10000); // Default to 10 seconds (10000 ms)
  const [labels, setLabels] = useState([]);
  const [voltageData, setVoltageData] = useState([]);
  const [currentData, setCurrentData] = useState([]);

  useEffect(() => {
    const timer = setInterval(() => {
      socket.emit("requestData"); // Ask the server for the latest data (could be an event or poll)
    }, interval);

    return () => clearInterval(timer);
  }, [interval]);

  useEffect(() => {
    socket.on("serialData", ({ voltage, current, time }) => {
      setLabels((prev) => [...prev.slice(-19), time]);
      setVoltageData((prev) => [...prev.slice(-19), voltage]);
      setCurrentData((prev) => [...prev.slice(-19), current]);
    });

    return () => socket.off("serialData");
  }, []);

  const renderContent = () => {
    if (activeTab === "current") {
      return <Graph type="current" labels={labels} data={currentData} />;
    } else if (activeTab === "voltage") {
      return <Graph type="voltage" labels={labels} data={voltageData} />;
    } else if (activeTab === "download") {
      return (
        <div style={{ marginTop: "50px" }}>
          <h3>Download the recorded data</h3>
          <button
            onClick={() => {
              window.location.href = "http://localhost:5000/download-csv";
            }}
            style={{
              padding: "12px 24px",
              fontSize: "16px",
              backgroundColor: "#28a745",
              color: "white",
              border: "none",
              borderRadius: "8px",
              cursor: "pointer",
              marginTop: "20px",
            }}
          >
            Download Data (CSV)
          </button>
        </div>
      );
    }
    return null;
  };

  return (
    <div>
      <nav
        style={{
          display: "flex",
          justifyContent: "space-around",
          backgroundColor: "#343a40",
          padding: "15px 0",
        }}
      >
        {["current", "voltage", "download"].map((tab) => (
          <span
            key={tab}
            onClick={() => setActiveTab(tab)}
            style={{
              color: "#fff",
              fontWeight: activeTab === tab ? "bold" : "normal",
              fontSize: "18px",
              cursor: "pointer",
              borderBottom: activeTab === tab ? "2px solid #fff" : "none",
              paddingBottom: "5px",
            }}
          >
            {tab.charAt(0).toUpperCase() + tab.slice(1)}
          </span>
        ))}
      </nav>

      {/* Interval Input */}
      <div style={{ textAlign: "center", marginTop: "20px" }}>
        <label htmlFor="interval">Select Graph Update Interval: </label>
        <select
          id="interval"
          value={interval}
          onChange={(e) => setIntervalValue(Number(e.target.value))}
        >
          <option value={10000}>10 Seconds</option>
          <option value={20000}>20 Seconds</option>
          <option value={60000}>1 Minute</option>
          <option value={3600000}>1 Hour</option>
          <option value={86400000}>1 Day</option>
        </select>
      </div>

      {/* Main Content */}
      <div style={{ padding: "30px" }}>
        {activeTab !== "download" && (
          <div style={{ display: "none" }}>
            <Graph
              type={activeTab}
              labels={labels}
              data={activeTab === "voltage" ? voltageData : currentData}
            />
          </div>
        )}
        {renderContent()}
      </div>
    </div>
  );
};

export default App;
